<template>
  <el-select
    filterable
    remote
    reserve-keyword
    placeholder="Search Value"
    v-model="value"
    :remote-method="remoteMethod"
    :value="value"
    @change="handleChange"
  >
    <el-option
      v-for="({ title, location_id, event_id }, key) in options"
      :key="key"
      :label="title"
      :value="location_id || event_id"
    />
  </el-select>
</template>

<script>
  export default {
    name: 'Selectable',
    data: () => ({
      value: ''
    }),
    props: {
      options: {
        type: Array,
        default: () => []
      },
      generalKey: {
        type: String,
        default: ''
      }
    },
    methods: {
      remoteMethod(query) {
        this.$emit('onSearch', query)
      },
      handleChange(value) {
        this.$emit('onChange', {
          [this.generalKey]: value
        })
      }
    }
  }
</script>



